import React, { useState }from 'react';
import '../books/books.css';
import Book from '../books/Book';
import {Books} from '../books/Books';

const Menu = () => {

    const showHorizontalMenu = () => {

        var links = document.getElementById("links");

        if(window.innerWidth < 680){
            links.style.display = "none";
        } else {
            links.style.display = "flex";
        }
        
    }

    const displayBurguerMenu = () => {

        var links = document.getElementById("links");

        if(links.style.display === "none"){
            links.style.display = "block";
        } else {
            links.style.display = "none";        
        }
    }

    window.onresize = showHorizontalMenu;

    var booklist = Books.map(
        (book) => {
        return (
            // {...book} extender propiedades
            <Book key = {book.id} {...book}></Book>
        );
        }
    )

    const [, setBooks] = useState();

    function sortBy(category){

        var sorted;
        
        if(category === 'titulo'){

            sorted = Books.sort((a,b) => (a.titulo > b.titulo) ? 1 : -1).map(
                (book) => {
                return (
                    // {...book} extender propiedades
                    <Book key = {book.id} {...book}></Book>
                );
                }
            );

        } else if(category === 'id'){

            sorted = Books.sort((a,b) => (a.id > b.id) ? 1 : -1).map(
                (book) => {
                return (
                    // {...book} extender propiedades
                    <Book key = {book.id} {...book}></Book>
                );
                }
            );
        
        }

        setBooks(sort => [sorted]);
    };

     
    return (
        <>
            <nav>
                <div id = 'links'>
                    <button onClick = {() => sortBy("id")}>Inicio</button>
                    <button onClick = {() => sortBy("titulo")}>A-Z</button>
                    <button onClick = {() => sortBy("autor")}>Autor (A-Z)</button>
                    <button href = '/'>Categorías</button>
                </div>

                <div id="search-container">
                <form action="#">
                    <input type="text" placeholder="Buscar..." name="search" />
                    <button type="submit"><i className="fa fa-search"></i></button>
                </form>
                </div>
                
                <button id="icon" tabIndex="0" onClick = {() => displayBurguerMenu()}>
                    <i className="fa fa-bars"></i>
                </button>
            </nav>
            <section className = 'booklist'>
                {booklist}
            </section>        
        </>
        
    );
}

export default Menu;